.Navbar{
    background-color: rgba(0,0,0,0.3);
    width: max-content;
    padding: 0.7rem 1.7rem;
    border-radius: 2em;
    z-index: 2;   
}

#Nav{
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    position:fixed;
    display: flex;
    gap: 0.8rem;
    backdrop-filter: blur(15px);
}

.Navbar a{
    background: transparent;
    padding: 0.1rem;

}

@media screen and (max-width : 600px){
    #Nav{
        position: fixed;
        left: 50%;
        font-size: 3em;
        margin-bottom: 0.6em;
    }
}


