.container.services{
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    gap: 12%;
}

.services_options{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

.services_option{
    background: aqua;
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}

#Contact{
    position: relative;
    top: -25em;
    gap: 1.3rem;

}

input,textarea{
    width: 40%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    position: relative;
    background: transparent;
    border: 2px solid aquamarine;
    display: grid;
    color: white;
    right: 25%;
}
button{
    width: 40%;
}

form{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
    position: relative;
    left: 55%;
}

.ContactIn{
    text-align: center;
}

.primary{
    width: 150px;
    height: 40px;
    background-color: aquamarine;
    color: black;
    font-family: Poppins;
    border-radius: 1.2rem;
    position: relative;
    left: 14em;

}

.primary:hover{
    background-color: aqua;
}


.Pic3{
    position: relative;
    right: 65em;
    top: 20em;


}

@media screen and (max-width : 600px){
    #Contact{
        position: relative;
        top: 110em;
        left: 40em;
        font-family: Poppins;
        color: white;
        height: 200vh;

    }

    .ContInfo{
        position: relative;
        right: 20%;
        font-size: 2.1em;

    }

    #onein{
        position: relative;
        left: -1.3em;
        top: 5.5em;
        font-size: 3em;
        white-space: nowrap;
    }

    #twoin{
        position: relative;
        top: 10.5em;
        left: 0.1em;
        font-size: 1.9em;

    }

    form{
        display: flex;
        flex-direction: column;
        gap: 1.2rem;
        position: relative;
        left: 70%;
        width: 100%;
        color: white;
 
 
    }


    input,textarea{
        width: 150%;
        padding: 1.2rem;
        border-radius: 0.5rem;
        background: transparent;
        border: 4px solid aquamarine;
        display: grid;
        font-size: 2em;
        top: 2em;
        font-family: Poppins;
        right: 130%;
        position: relative;
        text-align: center;
    }
input{
    margin-bottom: 1em;
}

    .primary{
        width: 280px;
        height: 80px;
        background-color: aquamarine;
        color: black;
        font-family: Poppins;
        border-radius: 1.2rem;
        position: relative;
        top: 3em;
        left: -39%;
        white-space: nowrap;
        font-size: 2em;
    
    }

    .Pic3{
        position: relative;
        right: 71em;
        width: 700px;
        top: 18em;

    }
}