header{
    height: 60vh;
    padding : 7rem;
    width: auto;

}


#Name{
    position: relative;
    left: -4em;
    top: -0.5em;
    font-size: 29px;
    display: absolute;
    animation: fade_down 2.5s;

}


.headercontainer{
    height: 100%;
    position: relative;
    margin-top: -4em;
    display: flex;
    flex-wrap:wrap;
    left: 2em;
}

/*===================== CTA ======================*/

.CTA{
    display:flex;
    position: relative;
    gap: -6em;
    top: -6em;
    left: 29em;
    text-align: center;
    font-size: small;
}

/*=======================Buttons=====================*/

#Home.btn{
    padding: 2px 2px;
    width: 100px;
}

#Exp.btn{
    padding: 2px 2px;
    width: 100px;
}

#Proj.btn{
    padding: 2px 2px;
    width: 100px;
}

#Cont.btn{
    padding: 2px 2px;
    width: 200px;

}
#Edu.btn{
    padding: 2px 2px;
    width: 200px;
}

.btn {
    display:inline-block;
    position: relative;
    transition-duration: 0.4s;
    border: 0.5px solid transparent;
    border-radius: 10px;
    width:300px;
    vertical-align: middle;
    text-align: center;
    right: 3em;
    white-space: nowrap;
    top: -1em;
    margin-right: 150px;
    height: 20px;
    animation: fade_down 2.5s;
    
  }
  
  
.btn:hover{
    background-color:aquamarine;
    color: black;
    border: 5px solid aquamarine;
  }


/*==========================Introduction message=====================*/


#Hi{
    color: white;
    font-size: 60px;
    position: relative;
    top: -0.5em;
}

.Aditya{
    color: aqua;
}

#Hi2{
    position: relative;
    margin-top: -2em;

}

#Hi3{
    position: relative;
    top:-60px;
    color: white;
    width: 60%;
    white-space: inherit;
    margin-top: 4em;
}

/*=========================================Image=================================*/

.IntroPic{
    position: relative;
    left: 70rem;
}

/*=======================================Social Media===========================*/

.social {
    position: relative;
    margin-top: -1em;
    display: flex;
    margin-right: 1em;
  }


  a.social:hover {
    transform: translateY(-4px);
  }

  img{
      width: 600px;
      position: relative;
      display: flex;
      flex-wrap: wrap;
      top: -22em;
      left: 40em;
      animation: mover 2s ease-in-out alternate infinite;
  }


  @keyframes fade_down {
    0% {
      opacity: 0.01;
      transform: translateY(-20px);
      transition: opacity 300ms cubic-bezier(0.645, 0.045, 0.355, 1),
        transform 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
    }
    100% {
        opacity: 1;
        transform: translateY(0px);
        transition: opacity 300ms cubic-bezier(0.645, 0.045, 0.355, 1),
          transform 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
      }
    }

    @keyframes fadeUp {
        0% {
        filter: alpha(opacity=0);
        opacity: 0;
        -webkit-transform: translateY(50%);
        -moz-transform: translateY(50%);
        -ms-transform: translateY(50%);
        -o-transform: translateY(50%);
        transform: translateY(50%);
    }
    100% {
        filter: alpha(opacity=100);
        opacity: 1;
        -webkit-transform: translateY(0);
        -moz-transform: translateY(0);
        -ms-transform: translateY(0);
        -o-transform: translateY(0);
        transform: translateY(0);
    }
}
@keyframes mover {
    0% { transform: translateY(0); }
    100% { transform: translateY(-10px); }
}



.HeaderIn{
    animation: fadeUp 2.5s;
    display: absolute;
    min-height: max-content;
    max-width: max-content;
    flex-wrap: wrap;
    margin-top: 8rem;
}


.hi,.hA{
    position:relative ;
    top: -2rem;


}


@media screen and (max-width : 600px){
  body{
    height: 60vh;
  }
  
  
    header{
    width:20vh;
    height: fit-content;
   }
   
    #Name{
        font-size: 2.1em;
        white-space: nowrap;
        padding: 1em 1em 1em 1em;
        padding-left: 3em;
        padding-top: 2em;


    }
    
    .HeaderIn{
        position: relative;
        top: -2.5em;
    }

    .CTA{
        display: none;
    }

    .headercontainer{
        font-size: 3em;
        padding: 1em 1em 1em 1em;
        padding-left: -6em;
        
    }

    #Social{
        padding: 1em;
        position: relative;
        right: 1em;
    }

    #Hi{
        font-size: 3em;

    }


    .IntroPic{
        width: 1000px;
        position: relative;
        left: -10%;
        top: 20%;
    }
}