.About{
    height: 60vh;
    padding : 7rem;
    overflow-x : hidden;
}


/*=========================Heading About===================*/
#Abtt{
    text-align: center;
    font-size: 3rem;
    position: relative;
    top: 2rem;

}

/*=========================2ndIntroPic=====================*/
#IntroPic2{
    position: relative;
    top: 4rem;
    left: 5rem;
    width: 40rem;
    animation: move 2s ease-in-out alternate infinite;
}

@keyframes mover {
    0% { transform: translateY(0); }
    100% { transform: translateY(-10px); }
}

@keyframes move {
    0% { transform: translateX(0); }
    100% { transform: translateX(-10px); }
}

/*=======================About Write Up Part===============*/
.AboutWrite{
    float: right;
    position: relative;
    top: -20rem;
    line-height: 2em;
    right: 5rem;
    word-wrap: normal;
    width: 35rem;
    font-size: 1.2em;
    animation: move 2s ease-in-out alternate infinite;
}


/*=======================Mobile Version==================*/

@media screen and (max-width : 600px){
    #Abtt{
        font-size: 6em;
        white-space: nowrap;
        left: 80%;
    }

    .AboutWrite{
        position: relative;
        left: 226%;
        font-size: 3em;
        width: 1110px;
        margin-top: -2em;
    

    }

    #IntroPic2{
        position: relative;
        top: 120em;
        left: 10em;
        width: 1000px;

    }


}
