/*=============================Full Stack Development Heading=================*/
#In{
    text-align: center;
}

/*=============================Coding Languages===============================*/
#Logo{
    position: relative;
    margin-top: 2em;
    margin-left: 17rem;
    display: flex;
    gap: 2rem;
    width: 2px;
    left: 10em;
    white-space: inherit;
}


/*=================Under Information for Full Stack Development==============*/
.Info{
    position: relative;
    top: 8em;
    text-align: center;
}

.experience{
    height: 100vh;
}

/*==================Mobile View==============================================*/



@media screen and (max-width : 600px){
   #In{
    font-size: 6em;
    position: relative;
    top: 22em;
    left: 1.6em;
    white-space: nowrap;
}

 #Logo{
    position: relative;
    top: 55em;
    left: -3.5em;
    padding-top: 6em;
}
  .Info{
    position: relative;
    left: 20%;
    top: 32em;
    font-size: 2em;
    width: 1210px;
    white-space: nowrap;

}
body{
    overflow-x: hidden;
}

}
