body{
	background : url(https://wallpapercave.com/wp/wp2757874.gif);
	background-size: 100% 50%;
	height: 100vh;
    font-family: Poppins;
    margin:0;
	color: white;
    padding: 0;
    border: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
	line-height: 1.7;
	overflow-x: hidden;
}

*{
	text-decoration: none;
	color: white;
	
}


@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

:root{
	--container-width-lg: 75%;
	--container-width-md: 86%;
	--container-width-sm: 90%;


}

html{
	scroll-behavior: smooth;
}


h1,h2,h3,h4,h5,h6{
	font-weight: 500;
}

h1{
	font-size: 2.5rem;
}

section{
	margin-top: 8rem;
	height: 100vh;
}

/*Make css for Text-light and A Hover!! */


a{
	
	transition: all 400ms ease;
}


.btn-primary{
	display: block;
	width: 100%;
	object-fit: cover;

}

@media screen and (max-width : 1024px){
	.container{
		width:var(--container-width-sm);
	}

	section{
		margin-top: 6rem;
	}
}

@media screen and (max-width : 600px){
	.container{
		width:var(--container-width-sm);

	}
	body{
		background-size:cover;
		margin:0;
		padding: 0;
		overflow-x: hidden;
	}
}